body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, Courier New, monospace;
}

.intellistyle {
  text-align: center;
  align-items: center;
  margin: auto;
}

.outerContainer {
  max-width: 100%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  justify-content: center;
  align-items: center;
  margin: auto;
  display: flex;
}

.container {
  max-width: 600px;
  min-width: 387px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  min-height: 500px;
  background-color: #f8f8f8;
  justify-content: center;
  align-items: center;
  padding: 50px;
  font-size: 12px;
  line-height: .2;
  display: flex;
}

.container img:hover {
  cursor: pointer;
  transition: all .2s ease-in-out;
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}

.main-products-wrap {
  max-height: -webkit-fit-content;
  max-height: -moz-fit-content;
  max-height: fit-content;
  max-width: -webkit-fit-content;
  max-width: -moz-fit-content;
  max-width: fit-content;
  position: relative;
}

.main-img {
  max-width: 210px;
  height: auto;
  position: relative;
}

.topRight {
  position: absolute;
  bottom: 62%;
  left: 56%;
}

.topRight:not(:lang(ae)):not(:lang(ar)):not(:lang(arc)):not(:lang(bcc)):not(:lang(bqi)):not(:lang(ckb)):not(:lang(dv)):not(:lang(fa)):not(:lang(glk)):not(:lang(he)):not(:lang(ku)):not(:lang(mzn)):not(:lang(nqo)):not(:lang(pnb)):not(:lang(ps)):not(:lang(sd)):not(:lang(ug)):not(:lang(ur)):not(:lang(yi)) {
  text-align: right;
}

.topRight:-webkit-any(:lang(ae), :lang(ar), :lang(arc), :lang(bcc), :lang(bqi), :lang(ckb), :lang(dv), :lang(fa), :lang(glk), :lang(he), :lang(ku), :lang(mzn), :lang(nqo), :lang(pnb), :lang(ps), :lang(sd), :lang(ug), :lang(ur), :lang(yi)) {
  text-align: left;
}

.topRight:is(:lang(ae), :lang(ar), :lang(arc), :lang(bcc), :lang(bqi), :lang(ckb), :lang(dv), :lang(fa), :lang(glk), :lang(he), :lang(ku), :lang(mzn), :lang(nqo), :lang(pnb), :lang(ps), :lang(sd), :lang(ug), :lang(ur), :lang(yi)) {
  text-align: left;
}

.topRight img {
  max-width: 130px;
  height: auto;
  max-height: 160px;
}

.bottomLeft {
  position: absolute;
  bottom: 6%;
  right: 75%;
}

.bottomLeft img {
  max-width: 100px;
  height: auto;
}

.footwearDiv {
  position: absolute;
  bottom: -3%;
  left: 72%;
}

.footwearDiv img {
  max-width: 100px;
  height: auto;
}

.topLeft {
  text-align: initial;
  position: absolute;
  bottom: 62%;
  right: 68%;
}

.topLeft img {
  max-width: 170px;
  height: auto;
  max-height: 150px;
}

.moodboard-price {
  font-weight: bold;
}

s.cross-text {
  color: red;
}

.outerContainer button {
  cursor: pointer;
  background: none;
  border: none;
  outline: none;
}

@media screen and (min-width: 1024px) {
  .container {
    margin: 1%;
  }
}

